// Config API https://www.kancloud.cn/yunye/axios/234845
import {getHeaders} from './util'

//const BASE_URL = 'http://localhost:8082/'
const BASE_URL = 'http://118.31.77.25:8081/'
export const STATIC_URL = BASE_URL

export const ERR_OK = 200

// GET
export function getRequest (url, params) {
  return {
    // `url`  URL
    url: url,
    // `method` 
    method: 'get', //  get
    // `baseURL`  `url`  `url`  URL
    //  `baseURL`  axios  URL
    baseURL: BASE_URL,
    // `headers` 
    headers: {'Accept': 'application/json;charset=UTF-8', 'Content-Type': 'application/json'},
    // `params`  URL 
    // (plain object) URLSearchParams 
    params: Object.assign(params, getHeaders()),
    // `timeout` (0 )
    //  `timeout` 
    timeout: 30000,
    // `withCredentials` 
    withCredentials: false, // 
    // `responseType`  'arraybuffer', 'blob', 'document', 'json', 'text', 'stream'
    responseType: 'json', // 
    // `xsrfCookieName`  xsrf token cookie
    xsrfCookieName: 'XSRF-TOKEN', // default
    // `xsrfHeaderName`  xsrf token  HTTP 
    xsrfHeaderName: 'X-XSRF-TOKEN', // 
    // `onUploadProgress` 
    onUploadProgress: function (progressEvent) {
      // 
    },
    // `onDownloadProgress` 
    onDownloadProgress: function (progressEvent) {
      // 
    },
    // `maxContentLength` 
    maxContentLength: 2000,
    // `validateStatus` HTTP  resolve  reject  promise  `validateStatus`  `true` ( `null`  `undefined`)promise  resolve; promise  rejecte
    validateStatus: function (status) {
      return status >= 200 && status < 300 // 
    },
    // `maxRedirects`  node.js  follow 
    // 0 follow 
    maxRedirects: 5 // 
  }
}

// POST
export function postRequest (url, dataAssign) {
  return {
    // `url`  URL
    url: url,
    // `method` 
    method: 'post', //  get
    // `baseURL`  `url`  `url`  URL
    //  `baseURL`  axios  URL
    baseURL: BASE_URL,
    // `transformRequest` 
    //  'PUT', 'POST'  'PATCH' 
    //  ArrayBuffer Stream
    // transformRequest: [function (data) {
    //   //  data 
    //   // return data
    //   return JSON.stringify(data)
    // }],
    // `transformResponse`  then/catch 
    transformResponse: [function (data) {
      //  data 
      return data
    }],
    headers: {'Content-type': 'application/x-www-form-urlencoded'},

    params: {
      token: window.sessionStorage.getItem('token')
      },
    // `params`  URL 
    // (plain object) URLSearchParams 
   // params: getHeaders(),
    // `data` 
    //  'PUT', 'POST',  'PATCH'
    //  `transformRequest` 
    // - string, plain object, ArrayBuffer, ArrayBufferView, URLSearchParams
    // - FormData, File, Blob
    // - Node  Stream
    data: dataAssign,
    // `timeout` (0 )
    //  `timeout` 
    timeout: 30000,
    // `withCredentials` 
    withCredentials: false, // 
    // `responseType`  'arraybuffer', 'blob', 'document', 'json', 'text', 'stream'
    responseType: 'json', // 
    // `xsrfCookieName`  xsrf token cookie
    xsrfCookieName: 'XSRF-TOKEN', // default
    // `xsrfHeaderName`  xsrf token  HTTP 
    xsrfHeaderName: 'X-XSRF-TOKEN', // 
    // `onUploadProgress` 
    onUploadProgress: function (progressEvent) {
      // 
    },
    // `onDownloadProgress` 
    onDownloadProgress: function (progressEvent) {
      // 
    },
    // `maxContentLength` 
    maxContentLength: 2000,
    // `validateStatus` HTTP  resolve  reject  promise  `validateStatus`  `true` ( `null`  `undefined`)promise  resolve; promise  rejecte
    validateStatus: function (status) {
      return status >= 200 && status < 300 // 
    },
    // `maxRedirects`  node.js  follow 
    // 0 follow 
    maxRedirects: 5 // 
  }
}



