export function formatDate (date, fmt) {
  if (/(y+)/.test(fmt)) {
    fmt = fmt.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length))
  }
  let o = {
    'M+': date.getMonth() + 1,
    'd+': date.getDate(),
    'h+': date.getHours(),
    'm+': date.getMinutes(),
    's+': date.getSeconds()
  }
  for (let k in o) {
    if (new RegExp(`(${k})`).test(fmt)) {
      let str = o[k] + ''
      fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? str : padLeftZero(str))
    }
  }
  return fmt
};

function padLeftZero (str) {
  return ('00' + str).substr(str.length)
};

// Promise 
function Post (url, data) {
  return new Promise(function (resolve, reject) {
    axios(postRequest(url, data)).then((res) => {
      if (res.status === ERR_OK) {
        return resolve(res.data)
      } else {
        VUE.prototype.$toast(res.data.message, 'error')
        if (res.data.statusCode === 401) {
          setTimeout(() => {
            Router.push('/standby')
          }, 3000)
        }
        return reject(res.data.data)
      }
    }).catch((err) => {
      console.log('err', err)
      VUE.prototype.$toast('e-iCall021-80205077', 'error')
      return reject(err)
    })
  })
}

function Get (url, params) {
  VUE.prototype.$loading()
  return new Promise(function (resolve, reject) {
    axios(getRequest(url, params)).then((res) => {
      VUE.prototype.$loading.close()
      if (res.data.statusCode === ERR_OK) {
        return resolve(res.data)
      } else {
        VUE.prototype.$toast(res.data.message, 'error')
        if (res.data.statusCode === 401) {
          setTimeout(() => {
            Router.push('/standby')
          }, 3000)
        }
        return reject(res.data.message)
      }
    }).catch((err) => {
      console.log('err', err)
      VUE.prototype.$loading.close()
      VUE.prototype.$toast('e-iCall021-80205077', 'error')
      return reject(err)
    })
  })
}



